	module.exports = {"data":{"site":{"siteMetadata":{"title":"Саентология Тома Круза","author":"Том Круз"}},"markdownRemark":{"id":"/home/gleki/ccc/src/pages/2016-05-19-preved/index.md absPath of file >>> MarkdownRemark","html":"<p>Здравствуйте, меня зовут Рон Хаббард, и я алкоголик.</p>\n<p>\n  <a\n    class=\"gatsby-resp-image-link\"\n    href=\"/static/15b64b1e947f3a2d759a2acfa4cfb49f-768a1.jpg\"\n    style=\"display: block\"\n    target=\"_blank\"\n    rel=\"noopener\"\n  >\n  \n  <span\n    class=\"gatsby-resp-image-wrapper\"\n    style=\"position: relative; display: block; ; max-width: 255px; margin-left: auto; margin-right: auto;\"\n  >\n    <span\n      class=\"gatsby-resp-image-background-image\"\n      style=\"padding-bottom: 71.42857142857143%; position: relative; bottom: 0; left: 0; background-image: url('data:image/jpeg;base64,'); background-size: cover; display: block;\"\n    >\n      <img\n        class=\"gatsby-resp-image-image\"\n        style=\"width: 100%; margin: 0; vertical-align: middle; position: absolute; top: 0; left: 0; box-shadow: inset 0px 0px 0px 400px white;\"\n        alt=\"Хаббард в 1950 году\"\n        title=\"\"\n        src=\"/static/15b64b1e947f3a2d759a2acfa4cfb49f-768a1.jpg\"\n        srcset=\"/static/15b64b1e947f3a2d759a2acfa4cfb49f-f2587.jpg 148w,\n/static/15b64b1e947f3a2d759a2acfa4cfb49f-a3144.jpg 295w,\n/static/15b64b1e947f3a2d759a2acfa4cfb49f-768a1.jpg 532w\"\n        sizes=\"(max-width: 255px) 100vw, 255px\"\n      />\n    </span>\n  </span>\n  \n  </a>\n    </p>\n<p>А если честно … кто же он такой и что же такое саентология?                                                                                 </p>\n<hr>\n<h1>Телема</h1>\n<p>Всё началось в 1940-х годах.</p>\n<p>Большой популярностью в те годы пользовался основатель движения Телема Алистер Кроули. Мистик, наркоман, поэт.</p>\n<p>В 1947 году он перешёл на новые высшие планы бытия, и вакуум здесь на этой тленной планете должен был чем-то заполниться.</p>\n<p>Заполнился он одним из верных прихожан церкви Кроули, а звали его Рон Хаббард.</p>\n<p>Неудавшийся военный Лафайетт Рональд Хаббард родился в 1913 году. В начале 1940-х годов Хаббард зарабатывал на жизнь посредственными романами на тему космоса и научной фантастики, которые он печатал с неимоверной скоростью (и писательская деятельность, как мы увидим позже, имела колоссальное значение для его последующей деятельности).</p>\n<p>Однако, понимая, что на писательском поприще много не заработаешь, Хаббард начинает интересоваться возможностью захвата мира. Да-да, вы не ослышались. В 1940-е годы он штудирует труды известных психиатров, общается с успешным психиатром капитаном Томпсоном, который знакомит его с технологией работы с пациентами, пережившими ужасы войны.</p>\n<p>В 1947 году после смерти Кроули Хаббард понимает, что настало время встать на место Кроули.</p>\n<h1>Дианетика</h1>\n<p>В 1948 году он готовит труд “Дианетика”, описывающий технологию управления сознанием. Фактически, это украденная Хаббардом система командного гипноза по Фрейду, доработанная капитаном Томпсоном и без ссылок на эти источники.</p>\n<p>При содействии редактора журнала “Amazing Stories” (Необыкновенные истории), публиковавшего бульварные фантастические романы, Хаббард публикует 9 мая 1945 года свою “книгу номер 1” - “Дианетика: современная наука душевного здоровья.”</p>\n<p>Уже через год после публикации вначале положительные отзывы о книге сменяются серьёзной критикой, отсутствием положительных результатов применения этой “науки”. Созданное книгой движение распадается.</p>\n<h1>Саентология</h1>\n<p>1952 год. Получив с продаж “Дианетики” значительные финансовые средства, и понимая, что на научных основах свой проект вести более невозможно, Хаббард начинает разрабатывать новый проект “Церкви саентологии”. </p>\n<p>Фактически, это был чисто коммерческий проект, которому требовался религиозный статус для освобождения от уплаты налогов.</p>\n<p>Саентология утверждает, что каждый человек представляет из себя бессмертную душу - тэтана, существующего <strong>триллионы</strong> лет.</p>\n<p>Однако, здесь на Земле к каждому тэтану прицепилась куча других тэтанов.</p>\n<p>Причиной этого стала катастрофа, случившаяся 75 миллионов лет назад, когда галактический тиран Зену (вспомним, кем был Хаббард ранее!) сбросил людей на вулканы на планете Земля, после чего эта планета просто стала кишеть тэтанами.</p>\n<p>С помощью дорогостоящих процедур, напоминающих психоанализ, возможно “отцепить” этих тэтанов и достичь просветления.</p>\n<p>Итак, Хаббард попытался заставить поверить людей в написанный им очередной фантастический роман.</p>\n<p>Общая стоимость курсов “очищения” (клиринга, клирования, просветления) составляет до нескольких миллионов долларов.</p>\n<p>И люди платят! Не так много найдется таких людей, но они есть!</p>\n<h1>Эти дни</h1>\n<p>В эти дни число саентологов составляет в США около 50 000 человек (включая представителей Нации Ислама - еретического течения в исламе). В других странах доля саентологов ещё меньше.</p>\n<h1>Результаты</h1>\n<p>Результатом практики саентологии часто могут быть:</p>\n<ol>\n<li>\n<p>Очистили не только вас от духов, но и от денег</p>\n</li>\n<li>\n<p>проблемы с психикой</p>\n</li>\n<li>\n<p>годы потерянного времени (общая длительность процедур занимает годы)</p>\n</li>\n</ol>\n<h1>Что теперь?</h1>\n<p>Мы начнём с изучения как базовых книг, так и секретных томов, написанных Хаббардом.</p>\n<p>P.S. И Хаббард действительно был алкоголиком.</p>","frontmatter":{"title":"Меня зовут Хаббард ...","date":"April 19, 2016"}}},"pathContext":{"path":"/preved/"}}

